@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body{background-color:#000;font-family:'Montserrat', sans-serif;margin:0;padding:25px}.modal-wrapper{position:fixed;top:0;left:0;bottom:0;right:0;background-color:rgba(0,0,0,0.6)}.modal{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);background-color:black;padding:25px;max-width:90vw;max-height:90vh;border-radius:5px}.modal img{display:block;width:100%;max-width:300px;margin-bottom:25px;border-radius:5px}.modal p{color:#fff;font-weight:bold;text-align:center;text-transform:uppercase}.modal .buttons{width:100%;display:flex;justify-content:space-between}.modal button{font-family:'Montserrat';background-color:#f37040;border:2px solid #d35806;color:#fff;padding:10px;letter-spacing:0.1em;outline:none;border-radius:5px;cursor:pointer}.dog__wrapper{display:grid;align-items:center;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr))}.dog__item{display:flex;flex-direction:column;width:auto;margin:15px;padding:10px 20px;background-color:#c35922;border-radius:4px;transition:all .5s}.dog__item p{font-size:18px;font-weight:bold;line-height:1em;letter-spacing:0.1em;color:#fff;text-transform:capitalize}.dog__item:hover{background-color:#c0250a}button.red{background-color:#c0250a;border:2px solid #d40707}

